<template>
  <div class="checkout-address">
    <div class="text">
      <div v-if="!showAddress" class="none">
        您需要先添加收货地址才可提交订单。
      </div>
      <ul v-else>
        <li>
          <span>收<i />货<i />人：</span>{{ showAddress.receiver }}
        </li>
        <li>
          <span>联系方式：</span
          >{{ showAddress.contact.replace(/^(\d{3})\d{4}(\d{4})/, "$1****$2") }}
        </li>
        <li>
          <span>收货地址：</span>{{ showAddress.fullLocation }}{{ address }}
        </li>
      </ul>
      <a
        @click="openAddressEdit(showAddress)"
        v-if="showAddress"
        href="javascript:;"
        >修改地址</a
      >
    </div>
    <div class="action">
      <XtxButton @click="openDialog()" class="btn">切换地址</XtxButton>
      <XtxButton @click="openAddressEdit({})" class="btn">添加地址</XtxButton>
    </div>
  </div>
  <!-- 切换收货地址 -->
  <XtxDialog v-model:visible="visibleDialog" title="切换收货地址">
    <div
      @click="selectedAddress = item"
      :class="{ active: selectedAddress && selectedAddress.id === item.id }"
      class="text item"
      v-for="item in list"
      :key="item.id"
    >
      <ul>
        <li>
          <span>收<i />货<i />人：</span>{{ item.receiver }}
        </li>
        <li>
          <span>联系方式：</span
          >{{ item.contact.replace(/^(\d{3})\d{4}(\d{4})/, "$1****$2") }}
        </li>
        <li>
          <span>收货地址：</span
          >{{ item.fullLocation.replace(/ /g, "") + item.address }}
        </li>
      </ul>
    </div>
    <template #footer>
      <XtxButton
        @click="visibleDialog = false"
        type="gray"
        style="margin-right: 20px"
        >取消</XtxButton
      >
      <!-- visibleDialog = false -->
      <XtxButton @click="confirmAddressFn" type="primary">确认</XtxButton>
    </template>
  </XtxDialog>
  <!-- 收货地址添加编辑组件 -->
  <AddressEdit @on-success="successHandler" ref="addressEditCom" />
</template>
<script>
import AddressEdit from './address-edit.vue'
import { ref } from 'vue'
export default {
  name: 'CheckoutAddress',
  components: { AddressEdit },
  props: {
    // 收货地址列表
    list: {
      type: Array,
      default: () => []
    }
  },
  // 1.在拥有根元素的组件中，触发自定义使用，有没有emits选项无所谓
  // 2.如果你的组件渲染的代码片段，vue3.0规范，需要在emits中申明你所触发的自定义事件
  // 3.提倡：你触发自定义事件，需要在emit选项申明下，代码可读性高
  emits: ['change'],
  setup (props, { emit }) {
    // 1.找到默认收货地址
    // 2.没有默认收货地址，使用第一条收货地址
    // 3.如果没有地址数据，提示请添加地址
    const showAddress = ref(null)
    // 默认地址
    const defaultAddress = props.list.find(item => item.isDefault === 0)
    if (defaultAddress) {
      showAddress.value = defaultAddress
    } else {
      if (props.list.length) {
        // 加上一个注释：忽略下面代码的检查eslint
        // eslint-disable-next-line vue/no-setup-props-destructure
        showAddress.value = props.list[0]
      }
    }
    // 默认通知父组件一个收货地址ID
    emit('change', showAddress.value && showAddress.value.id)
    //  打开关闭对话框
    const visibleDialog = ref(false)

    // 选择的地址
    const selectedAddress = ref(null)
    // 切换成功，用选中的地址替换显示地址
    const confirmAddressFn = () => {
      // 确认地址：显示的地址换成你选中的地址
      showAddress.value = selectedAddress.value
      // 把你选中的地址id通知结算组件
      // ?取值的前置判断
      emit('change', selectedAddress.value?.id)
      visibleDialog.value = false
    }
    const openDialog = () => {
      // 数据使用完毕置空
      selectedAddress.value = null
      // 打开对话框
      visibleDialog.value = true
    }
    // 添加地址的对话框
    // 调用组件实例
    const addressEditCom = ref(null)
    const openAddressEdit = (address) => {
      // console.log('地址对象：', address)
      // 调用子组件的方法，打开对话框
      addressEditCom.value.open(address)
    }
    // 从添加地址对话框获取数据进行列表展示的方法
    const successHandler = (formData) => {
      // 根据formData中的ID去当前地址列表中查找，有就是修改，否则是添加
      // 去已存在的地址列表中查找是否存在该id，如果存在就是修改地址（找到该地址）
      const address = props.list.find(item => item.id === formData.id)
      if (address) {
        // 修改
        for (const key in address) {
          address[key] = formData[key]
        }
      } else {
        // 如果是添加：往list中追加一条
        // 当你在修改forData的时候，数组中的数据也会更新，因为是同一个引用地址
        // 啥时候修改formData，当你打开对话框，需要清空之前的输入信息
        // 克隆formData数据
        const jsonStr = JSON.stringify(formData)

        // props警告：但是list是复杂数据修改是没问题的
        // eslint-disable-next-line vue/no-mutating-props
        props.list.unshift(JSON.parse(jsonStr))
      }
    }
    return {
      showAddress,
      visibleDialog,
      selectedAddress,
      confirmAddressFn,
      openDialog,
      openAddressEdit,
      addressEditCom,
      successHandler
    }
  }
}
</script>
<style scoped lang="less">
.xtx-dialog {
  .text {
    flex: 1;
    min-height: 90px;
    display: flex;
    align-items: center;
    &.item {
      border: 1px solid #f5f5f5;
      margin-bottom: 10px;
      cursor: pointer;
      &.active,
      &:hover {
        border-color: @xtxColor;
        background: lighten(@xtxColor, 50%);
      }
      > ul {
        padding: 10px;
        font-size: 14px;
        line-height: 30px;
      }
    }
  }
}
.checkout-address {
  border: 1px solid #f5f5f5;
  display: flex;
  align-items: center;
  .text {
    flex: 1;
    min-height: 90px;
    display: flex;
    align-items: center;
    .none {
      line-height: 90px;
      color: #999;
      text-align: center;
      width: 100%;
    }
    > ul {
      flex: 1;
      padding: 20px;
      li {
        line-height: 30px;
        span {
          color: #999;
          margin-right: 5px;
          > i {
            width: 0.5em;
            display: inline-block;
          }
        }
      }
    }
    > a {
      color: @xtxColor;
      width: 160px;
      text-align: center;
      height: 90px;
      line-height: 90px;
      border-right: 1px solid #f5f5f5;
    }
  }
  .action {
    width: 420px;
    text-align: center;
    .btn {
      width: 140px;
      height: 46px;
      line-height: 44px;
      font-size: 14px;
      &:first-child {
        margin-right: 10px;
      }
    }
  }
}
</style>
